.main{
	width: 1200px;
margin: 0 auto;
}
.banner{
	width: 1200px;
	margin: 0 auto;
	background: url("./images/banner_14.jpg");
	position: relative;
}
.swiper-button-prev{
	position: absolute;
	left: 0px;
}
.swiper-slide{
	text-align: center;
	padding-top: 28px
}
.swiper-container {
    width: 1100px;
    height: 405px;
    margin: 0 auto;
    position: relative;
}
.section_up{
	padding: 30px 50px 0 50px;
	background-color: #2b2e33;
	width: 1100px;
	margin: 0 auto;
  /*display: flex;*/
}
.zhuti{
	display: -ms-flexbox;
	display: flex;
}
aside{
	width: 630px;
	border-right: 2px solid #fff;
	padding-bottom: 12px;
	display: -ms-flexbox;
	display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
aside img{

}
aside h1{
	font-size: 36px;
	width: 400px;
	color: #fff;
	display: -ms-flexbox;
	display: flex;
	padding-left: 30px;
	padding-bottom: 35px;
}
aside .daojishi{
	display: -ms-flexbox;
	display: flex;
	width: 418px;
	margin-top: 60px;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
aside .daojishi>span:nth-child(1){
	font-size: 12px;
	color: #707378
}
.jishi{
	width: 418px;
	height: 64px;
	border-radius: 15px;
	background-color: #707378;
	padding: 0 22px;
	color: #fdd900;
  display: -ms-flexbox;
  display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;


}
.yushou{
  display: -ms-flexbox;
  display: flex;
}
.jishi span{
	font-weight: bold;
	font-size: 42px;
	line-height: 48px;
	width: 53px;
	text-align: center;

}
.jishi i{
	font-style: normal;
	font-size: 12px;
	line-height: 12px;
 -ms-flex: 1 1;
     flex: 1 1;
 margin-left: 40px;

}
.day_a{
	display: -ms-flexbox;
	display: flex;
	margin-left: 15px;
}
.day_b{
	display: -ms-flexbox;
	display: flex;
}
aside .daojishi>span:nth-child(3){
	font-size: 12px;
	color: #fff
}
aside .daojishi>span:nth-child(3)>span{
	color: #fdd900
}
article{
	padding-left: 50px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}
article>div{
	margin-bottom: 30px;
	display: -ms-flexbox;
	display: flex;
}
article>div>span:nth-child(1){
	width: 64px;
	height: 26px;
	line-height: 26px;
	background-color: #fff;
	border-radius: 10px;
	font-size: 12px;
	text-align: center;
	margin-right: 30px;
}
.dingjin b b{
	font-size: 36px;
}
.dingjin b{
	font-size: 24px;
	color: #fdd900;
}
.dingjin div span{
	font-size: 18px;
	color: #fff;
	line-height: 18px;
}
.kuanshi div a,.chima div a,.jishu a{
	width: 79px;
	line-height: 26px;
	font-size: 14px;
	margin-right: 5px;
	border-radius: 10px;
	border: 1px solid #fff;
	text-align: center;
	color: #fff;
}
.iphone{
	display: -ms-flexbox;
	display: flex;
}
.iphone a{
	text-decoration: none;
}
.chima{
	width: 380px;
	margin-bottom: 20px;

}
.chicun{
	display: -ms-flexbox;
	display: flex;
	width: 284px;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.chima div a{
	width: 64px;
	margin-bottom: 5px;
	text-decoration: none;
}
.jishu{
	display: -ms-flexbox;
	display: flex;
	width: 284px;
}
.jishu span{
	-ms-flex: 2 1;
	    flex: 2 1;
	text-align: center;
	color: #fff;
}
.jishu a{
	width: 69px;
	line-height: 38px;
	text-decoration: none;
}
.shuliang span:nth-child(3){
	display: -ms-flexbox;
	display: flex;
	line-height: 37px;
	width: 160px;
	text-align: center;
	color: #fff;
}
.anniu{
	padding: 20px 0;
	border-top: 2px solid #4f4f4f;
	border-bottom: 2px solid #4f4f4f;
	margin-top: 20px;
}
.anniu a{
	width: 1100px;
	border-radius: 15px;
	border: 3px solid #fdd900;
	display: block;
	line-height: 84px;
	text-align: center;
	font-size: 26px;
	font-weight: bold;
	color: #fdd900;
	text-decoration: none;
}
.bichu{
	margin-left: -50px;
	display: -ms-flexbox;
	display: flex;
	position: relative;
}
.buchuc{
	position: absolute;
	left: 0;
	top: 0;
}
.ditu{
	margin-bottom: -4px
}
article .cor{
	border: 1px solid #fdd900;
	color: #fdd900
}
article div .active{
	background-color: #fdd900;
	color: #ccc;
}



