section{
	background: url("./images/warp_18.jpg");
	width: 100%;
  display: -ms-flexbox;
  display: flex;
}
section .top_b,.wukong .top_c{
	width: 1200px;
	margin: 0 auto;
	padding-top: 24px;
	background-color: transparent;
}
.wukong .top_c{
	background: url("./images/top_03.jpg") no-repeat;
}
section .top_b h1,.wukong .top_c h1{
	line-height: 60px;
	font-size: 30px;
	border-left: 4px solid #000;
	margin-left: 50px;
	padding-left: 19px;
	color: #2a2e31;
  -ms-flex: 1 1;
      flex: 1 1
}
.shopp_on{
  display: -ms-flexbox;
  display: flex;
}
.shopp_on>span{
	line-height: 37px;
	font-size: 18px;
	border-bottom: 2px solid #000;
	margin-left: 20px;
}
.shopp_on span:nth-child(2){
	font-weight: bold;
	border-bottom: 4px solid #000
}
.shopp_on span:nth-child(4){
	margin-right: 50px;
}
.section_top{
	width: 1200px;
	margin: 0 auto;
	padding-bottom: 80px;
	background: url("./images/top_03.jpg") no-repeat;
	background-color: #fff;
  margin-bottom: -4px;
  /*display: none;*/
}
.shang{
	width: 1100px;
	margin: 0 auto;
	margin-top: 70px;
	border-bottom: 2px solid #2b2e33;
	padding-bottom: 10px;
  display: -ms-flexbox;
  display: flex;
}
.shang div{
	width: 26px;
	height: 26px;
	background: url("./images/huikuang_03.jpg");
	margin-right: 10px;
}
.shang span{
	font-size: 14px;
	padding-top: 5px;
}

.shang span:nth-child(2){
	margin-right: 30px;
}
.shang span:nth-child(3){
-ms-flex: 5 1;
    flex: 5 1;
}
.shang span:nth-child(4){
-ms-flex:2 1;
    flex:2 1
}
.shang span:nth-child(5){
-ms-flex:2 1;
    flex:2 1
}
.shang span:nth-child(6){
-ms-flex: 1 1;
    flex: 1 1;
}
.shang span:nth-child(7){
  margin-right: 17px;
}

.shangpin{
	width: 1100px;
	margin: 0 auto;
	border-bottom: 2px solid #000
}
.shangpin li{
	border-bottom: 1px solid #ccc;
	position: relative;
  display: -ms-flexbox;
  display: flex;
}
.left{
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}
.left div:nth-child(1){
	width: 26px;
	height: 26px;
	background: url("./images/huikuang_03.jpg");
	margin-left: 20px;
	margin-right: 48px;
	margin-top: 55px;
}
.left img{
	margin-top: 20px;
	margin-right: 10px;
	margin-bottom: 20px;
}
.shangpin li div:nth-child(3){
	width: 240px;
	margin-top: 23px;
}
.left a{
	font-size: 14px;
	color: #2a2d32;
  text-decoration: none;
}
.left span{
	color: #7b7c81;
	font-size: 12px;
	display: block;
}
.left span:nth-child(2){
	margin-top: 15px;
}
.right{
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  margin-top: 49px;
}
.right a{
  width: 36px;
  height: 32px;
  border: 1px solid #000;
  padding-top: 4px;
  text-align: center;
  text-decoration: none;
  color: #000;
}
.right>span:nth-child(1){
  -ms-flex: 1 1;
      flex: 1 1;
  margin-right: 30px;
}
.right span:nth-child(3){
  -ms-flex:2 1;
      flex:2 1;
  text-align: center;
}
.right span:nth-child(5){
  -ms-flex: 2 1;
      flex: 2 1;
  text-align: center;
  color: red;
}
.right a:nth-child(6){
  border: 0;
}
.xia{
  display: -ms-flexbox;
  display: flex;
}
.xia>span:nth-child(4){
  -ms-flex: 1 1;
      flex: 1 1;
}
.xia a{
  text-decoration: none;
}

.xia{
	width: 1100px;
	margin-left: 50px;
	background-color: #2b2e33;
	margin-top: 50px;
	position: relative;
}
.xia .clearb{
	width: 207px;
	display: none;
	left: -50px;
	top: -42px;
}
.xia .clearc{
	padding-top: 5px;
	padding-bottom: 10px;
	background: url("./images/clear3_06.jpg");
}
.xia .clearcc{
	padding-top: 5px;
	padding-bottom: 5px;
	background: url("./images/clear4_06.jpg");
}
.xia>div:nth-child(1){
	width: 26px;
	height: 26px;
	background: url("./images/baikuang_14.jpg");
	margin-left: 20px;
	margin-top: 17px;
	margin-right: 10px;

}
.xia span,.xia span>a{
	line-height: 60px;
	font-size: 14px;
	color: #fff;
}
.xia>span:nth-child(2){
	margin-right: 14px;
}
.xia span:nth-child(3){
	margin-right: 20px;
}
.xia span:nth-child(5){
	float: right;
	margin-right: 20px;
}
.xia span:nth-child(5) b,
.xia span:nth-child(5) b>span{
	color: #fdd900;
	font-size: 26px;
}
.xia span:nth-child(4)>span{
	color: #fdd900;
}
.qujiesuan{
	text-align: center;
	background-color: #fdd900;
	color: #2b2e33;
	width: 220px;
	line-height: 65px;
	font-size: 26px;
	font-weight: bold;
}
.zongshu{
	font-size: 20px;
}
.zongshu>span{
	color: red
}
.wrap .section_on .quanxuan{
	background: url("./images/huangduihao_07.jpg");
}
.clearb{
    display: none;
    position: absolute;
    bottom: 5px;
    right: 0;
    z-index: 20;
  }
  .clearc{
    background: url("./images/clear1_06.jpg");
    width: 89px;
    height: 26px;
    font-size: 14px;
    padding-left: 41px;
    padding-top: 15px;
    color: #fff;
}
.clearcc{
    background: url("./images/clear2_06.jpg");
    width: 65px;
    height: 26px;
    padding-top: 15px;
    padding-left: 12px;
    font-size: 14px;
    color: #fff;
}
.wukong{
	display: none;
  margin: auto;
  background-color: #fff;
  margin-bottom: -4px;

}
.wukong img{
	display: block;
	margin: 0 auto;
	margin-top: 90px;
	margin-bottom: 280px;
}
.hed{
  display: -ms-flexbox;
  display: flex;
}
.hed>span{
line-height: 37px;
font-size: 18px;
border-bottom: 2px solid #000;
margin-left: 20px;
}
.wukong .top_c{
  margin: 0 auto;
}



