.cart{
	width: 100%;
}
.cart .top_a{
	width: 1200px;
	margin: 0 auto;
	padding-top: 24px;

}
.cart .top_a{
	background: url("./images/top_03.jpg") no-repeat;
	background-color: #fff;
	margin-bottom: -4px;
}
.cart .top_a h1{
	line-height: 60px;
	font-size: 30px;
	border-left: 4px solid #000;
	margin-left: 50px;
	padding-left: 19px;
	color: #2a2e31;
}
.shangpin_a{
	width: 1100px;
	margin: 0 auto;
	margin-top: 87px;
	border-top: 2px solid #ccc;
	border-bottom: 2px solid #ccc;
	display: -ms-flexbox;
	display: flex;
}
.shangpin_a img:nth-child(1){
	margin: 9px 10px 9px 10px;
}
.shangpin_a h2:nth-child(2){
	width: 240px;
	margin-top: 30px;
	font-size: 18px;
	color: #292e32;

}
.guige{
	margin-left: 20px;
	height: 71px;
	margin-top: 19px;
	padding-top: 5px;
	border-left: 2px solid #fdd900;
	padding-left: 21px;
	-ms-flex: 2 1;
	    flex: 2 1;
}
.guige span{
	line-height: 21px;
	display: block;
	font-size: 14px;
	color: #717276;
}
.shangpin_a a:nth-child(4){
	width: 210px;
	line-height: 46px;
	height: 46px;
	text-align: center;
	font-size: 18px;
	margin-top: 34px;
	background-color: #fdd900;
	color: #000;
	text-decoration: none;
}
.shangpin_a a:nth-child(5){
	margin-right: 10px;
	width: 132px;
	height: 46px;
	line-height: 46px;
	color: #fff;
	text-align: center;
	margin-top: 34px;
	background-color: #2b2e33;
	text-decoration: none;
  /*display: flex;*/
}
.xiabian{
	width: 1130px;
	margin: 0 auto;
	margin-top: 75px;
	padding-bottom: 50px;
}
.xiangqing{
	display: -ms-flexbox;
	display: flex;
}
.xiangqing>div>img{
	display: block;
}
.xiangqing>div:last-child{
	margin-right: 0px;
}
/*.shopp_a span{
	font-size: 18px;
	color: #2b2e33;

}*/
.yellow{
	display: -ms-flexbox;
	display: flex;
}
.yellow span:nth-child(2){
	width: 980px;
	margin-top: 10px;
	background-color: #fdd900;
	height: 2px;
	margin-bottom: 15px;
	display: block;
}
.xiangqing>div{
	width: 196px;
	margin:30px 30px 0 0;
	padding-top: 50px;

}
.xiangqing>div>div{
 padding: 10px;
 border: 1px solid #ccc;
 border-top: 0px;
}
.xiangqing>div>div>span:nth-child(1){
	font-size: 14px;
}
.shopp_a{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}
.shopp_a>span{
	margin-top: 20px;
	color: red;
	font-size: 20px;
}
.shopp_a>a{
	width: 66px;
	height: 25px;
	background: url("./images/gouwudai_11.jpg");
	margin-top: 20px;
}



